<template>
    <div class="JzLoading">
        <div class="div0">
            <div class="div1">
                <img :src="imgSrc" />
                <span>请稍后...</span>
            </div>
        </div>
    </div>
</template>
<script>
export default ({
    name: "JzLoading",
    data() {
        return {
            imgSrc: "data:image/gif;base64,"
        }
    },
    mounted() {

    },
})
</script>
<style scoped>
@keyframes rotateImg {
    from {
        transform: rotate(0deg);
    }
    to {
        transform:  rotate(360deg);
    }
}

.JzLoading {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0);
}

.div0 {
    width: 100px;
    height: 100px;
    background: rgba(0, 0, 0, 0.7);
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 5px;
    transform: translate(-50%, -50%) rotate(0deg);
}

.div1 {
    width: 100%;
    height: 100%;
    background: transparent;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.div1 span {
    color: white;
    font-size: 0.98rem;
}

.div1 img {
    width: 30px;
    height: 30px;
    background: transparent;
    margin-bottom: 10px;
    animation: rotateImg 2s linear infinite;
}
</style>



