<template>
    <div class="addressinfo container">
        <NavigatorBar @clickLeftHandler="clickLeft" :title="title"></NavigatorBar>
        <div style="height:8px;"></div>
        <textarea :style="{color:greyColor}" class="area" placeholder="请输入联系地址" v-model="address"></textarea>
        <JzButton :title="buttonTitle" @clickButtonHandler="clickButton"></JzButton>
        <JzAlert dialogid="dialogid" v-show="pop" confirm="确认" @clickConfirmHandler="clickConfirm" :title="popTitle" :content="popContent"></JzAlert>

    </div>
</template>
<script>
import NavigatorBar from "@/components/NavigatorBar"
import JzButton from "@/components/JzButton"
import utils from "@/lib/utils"
import JzAlert from "@/components/JzAlert"

export default ({
    name: "AdressInfo",
    data: () => {
        return {
            title: '填写地址',
            buttonTitle: "确认",
            greyColor: utils.greyColor,
            address: "",
            dialogid: 1,
            pop: false,
            popTitle: '',
            popContent: ''
        }
    },
    created(){
        let address = this.$route.params.address
        if(!utils.isEmpty(address)){
            this.address = address
        }
    },
    methods: {
        clickLeft: function() {
            this.$router.back()
        },
        showJzAlert(title, content, dialogid) {
            this.pop = true
            this.popTitle = title
            this.popContent = content
            this.dialogid = dialogid
        },
        clickConfirm() {
            this.pop = false
            switch (this.dialogid) {
                case 1:
                    this.clickLeft()
                    break;
                case 2:
                    break;
                default:
                    break;
            }
        },
        clickButton: function() {
            if (this.address == "") {
                this.showJzAlert('提示', "联系地址不能为空")
                return
            }
            utils.jzPost("bopen/submitaddress", {
                bid: localStorage.getItem("bid"),
                address: this.address
            }, (result) => {
                if (result.code == 0) {
                    this.showJzAlert('提示', result.msg, 1)
                } else {
                    this.showJzAlert('提示', result.msg, 2)
                }
            })
        }
    },
    components: {
        NavigatorBar, JzButton, JzAlert
    }
})
</script>
<style scoped>
.area {
    width: 100%;
    height: 120px;
    background: white;
    border: none;
    resize: none;
    margin-bottom: 25px;
    padding: 15px;
    box-sizing: border-box;
    outline: none;
    font-size: 15px;
}
</style>


