import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import AddressInfo from '@/pages/AddressInfo'
import CheckIdInfo from '@/pages/CheckIdInfo'
import EmailInfo from '@/pages/EmailInfo'
import UploadAvatar from '@/pages/UploadAvatar'
import UploadPhoto from '@/pages/UploadPhoto'
import ProtocolReader from '@/pages/ProtocolReader'
import utils from '@/lib/utils'
import store from '@/store'
Vue.use(Router)

let router = new Router({
  mode: 'history',
  base: 'bopen',
  routes: [
    // {
    //   path: '/',
    //   redirect: {
    //     name: 'CheckIdInfo'
    //   }
    // },
    {
      path: '/',
      name: 'Home',
      title: '身份认证',
      component: Home
    },
    {
      path: '/addressinfo',
      name: 'AddressInfo',
      title: '填写地址',
      component: AddressInfo
    },
    {
      path: '/checkidinfo',
      name: 'CheckIdInfo',
      title: '证件信息核对',
      component: CheckIdInfo
    },
    {
      path: '/emailinfo',
      name: 'EmailInfo',
      title: '填写邮箱',
      component: EmailInfo
    },
    {
      path: '/uploadavatar',
      name: 'UploadAvatar',
      title: '拍照验证',
      component: UploadAvatar
    },
    {
      path: '/uploadphoto',
      name: 'UploadPhoto',
      title: '证件照片',
      component: UploadPhoto
    },
    {
      path: '/protocolreader',
      name: 'ProtocolReader',
      title: '阅读协议',
      component: ProtocolReader
    }
  ]
})
router.afterEach((transition) => {
  let name = transition.name
  let item = router.options.routes.filter((ele) => {
    return ele.name == name
  }).pop()
  let title = item.title
  utils.setTitle(title)
  if (utils.isWeiXin()) {
    store.state.showNavi = false
  } else {
    store.state.showNavi = true
  }
  if (name == "Home") {
    utils.callNative({
      action: 1,
      title: title,
      style: 'blue'
    })
  } else {
    utils.callNative({
      action: 1,
      title: title,
      style: 'white'
    })
  }
})
export default router



