<template>
  <div class="checkidinfo container">
    <NavigatorBar @clickLeftHandler="clickLeft" :title="title" :background="titleBg" :color="titleColor"></NavigatorBar>
    <div class="label"> 身份证姓名</div>
    <input class="txt" placeholder="请输入姓名" v-model="idinfo.name" />
    <div class="label"> 身份证号</div>
    <input class="txt" placeholder="请输入身份证号" v-model="idinfo.idno" />
    <div class="label"> 身份证有效日期</div>
    <div class="date">
      <div class="dateBegin item">
        <div class="item_content">
          <div class="item_grid">
            <div class="item">
              <span>起始有效日期</span>
              <span>{{idBegin}}</span>
            </div>
          </div>
          <div class="date-box">
            <input v-model="idBegin" type="date" class="date-cover" />
          </div>
        </div>
      </div>
      <div class="gap"></div>
      <div class="dateEnd item">
        <div class="item_content">
          <div class="item_grid">
            <div class="item">
              <span>截止有效日期</span>
              <span>{{idEnd}}</span>
            </div>
          </div>
          <div class="date-box">
            <input v-model="idEnd" type="date" class="date-cover" />
          </div>
        </div>
      </div>
    </div>
    <div class="label"> 身份证有效地址</div>
    <input class="txt" placeholder="请输入身份证有效地址" v-model="idinfo.idAddress" />
    <div style="height:25px;"></div>
    <div class="agreeLabel">
      <input type="checkbox" v-model="checked" />&nbsp&nbsp我已阅读并同意
      <span style="color:#4080fb" class="list-panel">
        <router-link class="routerLink" v-for="protocol in protocolList" :to="{name:'ProtocolReader',params:{protocolid:protocol.id,name:protocol.name,title:protocol.title}}" :key="protocol.id">{{protocol.name}}
        </router-link>
      </span>
    </div>
    <JzButton title="确认并提交" @clickButtonHandler="clickButton"></JzButton>
    <div style="height:30px;"></div>
    <JzAlert dialogid="dialogid" v-show="pop" confirm="确认" @clickConfirmHandler="clickConfirm" :title="popTitle" :content="popContent"></JzAlert>
  </div>
</template>
<script>
import NavigatorBar from "@/components/NavigatorBar"
import JzButton from '@/components/JzButton'
import utils from "@/lib/utils";
import JzAlert from "@/components/JzAlert"

export default {
  name: "CheckIdInfo",
  data() {
    return {
      title: '证件信息核对',
      titleBg: utils.whiteBgColor,
      titleColor: '#000',
      checked: false,
      idBegin: '1970-01-01',
      idEnd: '1970-01-01',
      idinfo: {
        name: '',
        idno: '',
        idBegin: '',
        idEnd: '',
        idAddress: ''
      },
      dialogid: 1,
      pop: false,
      popTitle: '',
      popContent: '',
      protocolList: [{
        id: 1,
        name: '协议名称',
      }]
    }
  },
  created: function() {
    this.idinfo = this.$route.query
    this.idBegin = this.idinfo.idBegin
    this.idEnd = this.idinfo.idEnd
    utils.jzPost("bopen/getprotocols", null, (result) => {
      if (result.code == 0) {
        this.protocolList = result.data
      } else {
        this.showJzAlert("提示", result.msg)
      }
    })
  },
  methods: {
    readProtocol(id) {
      this.$router.push({ name: "ProtocolReader", params: { id: id } })
    },
    clickLeft() {
      this.$router.back()
    },
    clickConfirm() {
      this.pop = false
    },
    showJzAlert(title, content) {
      this.pop = true
      this.popTitle = title
      this.popContent = content
    },
    clickButton() {
      if (!utils.isCnAndEn(this.idinfo.name)) {
        this.showJzAlert("提示", "姓名只能为中文汉字")
        return
      }
      if (this.idinfo.name == "") {
        this.showJzAlert("提示", "姓名不能为空")
        return
      }
      if (!utils.isCardID(this.idinfo.idno)) {
        this.showJzAlert("提示", "身份证号不正确")
        return
      }
      if (this.idinfo.idno == "") {
        this.showJzAlert("提示", "身份证号不能为空")
        return
      }
      if (this.idinfo.idBegin == "") {
        this.showJzAlert("提示", "身份证起始日期不能为空")
        return
      }
      if (this.idinfo.idEnd == "") {
        this.showJzAlert("提示", "身份证结束日期不能为空")
        return
      }
      if (this.idinfo.idAddress == "") {
        this.showJzAlert("提示", "身份证地址不能为空")
        return
      }
      if (!this.checked) {
        this.showJzAlert("提示", "请阅读并勾选用户投资协议")
      } else {
        utils.jzPost("bopen/submitidinfo", {
          bid: localStorage.getItem('bid'),
          name: this.idinfo.name,
          idno: this.idinfo.idno,
          idBegin: this.idBegin,
          idEnd: this.idEnd,
          idAddress: this.idinfo.idAddress,
          post_code: this.idinfo.post_code,
          sex: this.idinfo.sex,
          issuing_authority: this.idinfo.issuing_authority,
          birthday: this.idinfo.birthday
        }, (result) => {
          if (result.code == 0) {
            this.$router.push({ name: 'Home', query: { mobilephone: localStorage.getItem('mobilephone') } })
          } else {
            this.showJzAlert("错误", result.msg)
          }
        })
      }
    }
  },
  components: {
    NavigatorBar,
    JzButton,
    JzAlert
  },
}
</script>
 <style>
.label {
  height: 35px;
  font-size: 13px;
  color: #666666;
  padding-left: 15px;
  line-height: 35px;
  text-align: left;
}

.txtDiv {
  width: 100%;
  height: 45px;
  background: white;
  padding: auto 15px;
}

.txt {
  width: 100%;
  height: 45px;
  font-size: 15px;
  color: #333333;
  background: white;
  /* line-height: 45px; */
  /* display: flex;
  align-items: center; */
  border: none;
  outline: none;
  padding: 0 15px 0 15px;
  box-sizing: border-box;
}

.date {
  height: 80px;
  background: white;
  padding-left: 10px;
  padding-right: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.date span {
  font-size: 15px;
  color: #666666;
  margin: 3px auto;
}

.gap {
  width: 1px;
  background: #eeeeee;
  height: 35px;
}

.item {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-self: center;
}

.agreeLabel {
  margin: auto 15px;
  text-align: left;
  font-size: 12px;
  vertical-align: middle;
  margin-bottom: 25px;
}

.agreeLabel input {
  outline: none;
  border: none;
}

.item_content {
  width: 100%;
  height: 100%;
  background: transparent;
  position: relative;
}

.item_grid {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
}

.date-box {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
}

.date-cover {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  border: none;
  outline: none;
  background: transparent;
  opacity: 0;
}

.routerLink {
  text-decoration: none;
  color: #4080f8;
  font-size: 12px;
}
</style>


