<template>
    <div class="emailinfo container">
        <NavigatorBar @clickLeftHandler="clickLeft" :title="title"></NavigatorBar>
        <div style="height:8px;"></div>
        <textarea :style="{color:greyColor}" class="area" placeholder="请输入邮箱地址" v-model="email"></textarea>
        <JzButton :title="buttonTitle" @clickButtonHandler="clickButton"></JzButton>
        <JzAlert dialogid="dialogid" v-show="pop" confirm="确认" @clickConfirmHandler="clickConfirm" :title="popTitle" :content="popContent"></JzAlert>
    </div>
</template>
<script>
import NavigatorBar from "@/components/NavigatorBar"
import JzButton from "@/components/JzButton"
import utils from "@/lib/utils"
import JzAlert from "@/components/JzAlert"

export default ({
    name: "EmailInfo",
    data: () => {
        return {
            title: '填写邮箱',
            buttonTitle: "确认",
            greyColor: utils.greyColor,
            email: "",
            dialogid: 1,
            pop: false,
            popTitle: '',
            popContent: ''
        }
    },
    created() {
        let email = this.$route.params.email
        if (!utils.isEmpty(email)) {
            this.email = email
        }
    },
    methods: {
        clickLeft: function() {
            this.$router.back()
        },
        clickConfirm() {
            this.pop = false
            switch (this.dialogid) {
                case 1:
                    this.clickLeft()
                    break;
                case 2:
                    break;
                default:
                    break;
            }
        },
        clickButton: function() {
            if (this.email == "") {
                this.showJzAlert('提示', "邮箱地址不能为空")
                return
            }
            if (!utils.isEmail(this.email)) {
                this.showJzAlert('提示', "邮箱格式不正确")
                return
            }
            utils.jzPost("bopen/submitemail", {
                bid: localStorage.getItem("bid"),
                email: this.email
            }, (result) => {
                if (result.code == 0) {
                    this.showJzAlert('提示', result.msg, 1)
                } else {
                    this.showJzAlert('错误', result.msg, 2)
                }
            })
        },
        showJzAlert(title, content, dialogid) {
            this.pop = true
            this.popTitle = title
            this.popContent = content
            this.dialogid = dialogid
        },
    },
    components: {
        NavigatorBar, JzButton, JzAlert
    }
})
</script>
<style scoped>
.area {
    width: 100%;
    height: 120px;
    background: white;
    border: none;
    resize: none;
    margin-bottom: 25px;
    padding: 15px;
    box-sizing: border-box;
    outline: none;
    font-size: 15px;
}
</style>


