<template>
    <div class="JzAlert">
        <div class="dialog">
            <div class="title">{{title}}</div>
            <div class="content">{{content}}</div>
            <div class="footer">
                <div class="line"></div>
                <div class="buttons">
                    <div class="confirmBtn" @click="clickConfirm">{{confirm}}</div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
export default ({
    name:"JzAlert",
    data: function() {
        return {
         
        }
    },
    props: {
        title: {
            default: '提示'
        },
        content: {
            default: '这是一条提示内容'
        },
        confirm: {
            default: '确认'
        },
        cancel: {
            default: '取消'
        }
    },
    methods:{
        clickConfirm(){
            this.$emit('clickConfirmHandler');
        }
    }

})
</script>
<style scoped>
.JzAlert {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    background:rgba(0, 0, 0, 0.5);
}

.dialog {
    width: 70%;
    min-width: 270px;
    background: white;
    transform-origin: 50% 50%;
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: space-between;
}

.title {
    width: 100%;
    height: 40px;
    font-weight: bold;
    font-size: 17px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
}

.content {
    font-size: 15px;
    color: #333333;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 15px;
    margin-right: 15px;
    padding-top: 10px;
    padding-bottom: 10px;
}

.footer {
    height: 46px;
    width: 100%;
    bottom: 0;
}

.line {
    width: 100%;
    height: 1px;
    background: #eeeeee;
    /* background: black; */
}

.buttons {
    width: 100%;
    height: 45px;
}

.confirmBtn {
    width: 100%;
    height: 100%;
    color: #4080f8;
    font-size: 17px;
    display: flex;
    align-items: center;
    justify-content: center;
}
</style>


