<template>
    <div class="home container">
        <NavigatorBar @clickLeftHandler="clickLeft" :iconLeft="iconLeft" :title="title" :background="titleBg" :color="titleColor"></NavigatorBar>
        <div class="info" :style="{background:infoBg}">
            <div class="avatar">
                <img :src="avatar" />
            </div>
            <div class="desc" align="left">
                <label class="nickname">{{nickname}}</label>
                <label class="other">{{other}}&nbsp;&nbsp;{{idno}}</label>
            </div>
        </div>
        <div style="height:8px;"></div>
        <div class="itemPanel">
            <div v-for="(step,index) in stepList" :key="index">
                <div class="line"></div>
                <div class="item mobile" @click="clickStepItem(step)">
                    <div class='left'>
                        <div class="imgBoxLeft">
                            <img class="imgself" :src="step.icon" />
                        </div>
                        <label>{{step.stepname}}</label>
                    </div>
                    <div class="right">
                        <label v-if="step.status==0">未完成</label>
                        <label v-else-if="step.status==1">待审核</label>
                        <label v-else-if="step.status==2">已完成</label>
                        <label v-else>审核不通过</label>
                        <div v-show="step.status!=2" class="imgBoxRight">
                            <img class="imgself" src="static/arrow.png" />
                        </div>
                    </div>
                </div>
                <div v-show="(index+1)==stepList.length" class="line"></div>
            </div>
        </div>
    </div>
</template>
<script>
import NavigatorBar from "@/components/NavigatorBar"
import utils from "@/lib/utils"
import axios from "axios"
let id_front_img = null
let id_back_img = null
let face_img = null
export default ({
    name: 'Home',
    data: () => {
        return {
            title: '身份认证',
            iconLeft: "static/back.png",
            titleBg: utils.blueBgColor,
            titleColor: '#FFFFFF',
            infoBg: utils.blueBgColor,
            nickname: "经纪人_",
            other: '未实名',
            avatar: "static/avatar.jpg",
            mobilephone: "",
            bid: "",
            idno: "",
            //status:  0：未完成 1：待审核 2：已完善（不可再编辑） 3：审核不通过
            stepList: [
                {
                    step: 10,
                    icon: "static/mobile.png",
                    stepname: "手机",
                    status: 0,
                    addtion: {}
                },
                {
                    step: 20,
                    icon: "static/id.png",
                    stepname: "证件照片",
                    status: 0,
                    addtion: {}
                },
                {
                    step: 30,
                    icon: "static/face.png",
                    stepname: "人脸照片",
                    status: 0,
                    addtion: {}
                },
                {
                    step: 40,
                    icon: "static/email.png",
                    stepname: "邮箱",
                    status: 0,
                    addtion: {}
                },
                {
                    step: 50,
                    icon: "static/address.png",
                    stepname: "联系地址",
                    status: 0,
                    addtion: {}
                }
            ]
        }
    },
    created: function() {
        // axios.post("http://bdev.cc.gg/bopen/getstatus", 
        // {SID:"7668mgtj0r2dhcunq89fao3if2"},
        // {headers:{
        //     "Content-Type": "application/x-www-form-urlencoded;charset=utf-8"
        // }}
        // ).then(function(result) {
        //     console.log("AAAA:" + JSON.stringify(result.data))
        // })
    },
    mounted: function() {
        // axios.post("http://bdev.cc.gg/bopen/getstatus?SID=7668mgtj0r2dhcunq89fao3if2", {}).then(function(result) {
        //     console.log("BBB:" + JSON.stringify(result.data))
        // })

        this.mobilephone = this.$route.query.mobilephone
        this.bid = this.$route.query.bid
        let sid = this.$route.query.SID
        localStorage.setItem("SID", sid)
        let jsonObj = {}
        if (utils.isEmpty(this.mobilephone) && !utils.isEmpty(this.bid)) {//传bid
            jsonObj.bid = this.bid
            localStorage.setItem('bid', this.bid)
        } else if (!utils.isEmpty(this.mobilephone) && utils.isEmpty(this.bid)) {//传手机号
            jsonObj.mobilephone = this.mobilephone
            localStorage.setItem('mobilephone', this.mobilephone)
        } else if (!utils.isEmpty(this.mobilephone) && !utils.isEmpty(this.bid)) {
            jsonObj.bid = this.bid
            localStorage.setItem('bid', this.bid)
        } else {
            if (utils.isEmpty(sid)) {
                alert("缺少bid、手机号、SID")
                return
            }
        }
        //手机号查询用户开户信息
        utils.jzPost("bopen/getstatus", jsonObj, (result) => {
            if (result.code == 0) {
                let data = result.data
                let avatar = data.avatar
                if (!utils.isEmpty(avatar)) {
                    this.avatar = avatar
                }
                if (data.isRealName) {
                    this.other = "已实名"
                    this.idno = data.idno.replace(/^(\d{3})\d{11}(\d+)/, "$1***********$2")
                } else {
                    this.other = "未实名"
                }
                this.stepList.map((item) => {
                    let obj = data.steps.filter((dataItem) => {
                        return dataItem.step == item.step
                    }).pop()
                    item.status = obj.status
                    item.addtion = obj.addtion
                })
                if (data.avatar != "") {
                    this.avatar = data.avatar
                }
                this.nickname = data.name
                localStorage.setItem('bid', data.bid)
            } else {
                alert(result.msg)
            }
        })
    },
    components: {
        NavigatorBar,
    },
    methods: {
        clickLeft: () => {
            utils.callNative({ action: 2 })
        },

        clickStepItem: function(info) {
            switch (info.step) {
                case 10:
                    if (info.status != 2) {
                        this.$router.push({ name: "Home" })
                    }
                    break;
                case 20:
                    if (info.status != 2) {
                        this.$router.push({ name: "UploadPhoto", query: info.addtion })
                    }
                    break;
                case 30:
                    if (info.status != 2) {
                        this.$router.push({ name: "UploadAvatar", params: info.addtion })
                    }
                    break;
                case 40:
                    if (info.status != 2) {
                        this.$router.push({ name: "EmailInfo", params: info.addtion })
                    }
                    break;
                case 50:
                    if (info.status != 2) {
                        this.$router.push({ name: "AddressInfo", params: info.addtion })
                    }
                    break;
                default:
                    break;
            }
        }
    }
})
</script>
<style scoped>
.info {
    height: 64px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-left: 15px;
}

.avatar {
    width: 45px;
    height: 45px;
}

.avatar img {
    width: 100%;
    height: 100%;
}

.desc {
    height: 45px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-self: center;
    margin-left: 10px;
    color: white;
}

.nickname {
    font-size: 16px;
}

.other {
    font-size: 13px;
    display: flex;
    justify-content: flex-start;
}

.itemPanel {
    background: white;
    width: 100%;
    height: auto;
}

.item {
    height: 45px;
    background: white;
    padding-left: 15px;
    padding-right: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}

.item .left {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.item .left label {
    font-size: 15px;
    color: #333333;
}

.item .right {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.item .right label {
    color: #999999;
    font-size: 15px;
}

.line {
    height: 1px;
    background: #eeeeee;
    margin-left: 15px;
}

.imgBoxLeft {
    width: 20px;
    height: 20px;
    margin-right: 15px;
}

.imgBoxRight {
    width: 20px;
    height: 20px;
    margin-left: 0;
}

.imgself {
    height: 100%;
    float: right;
}
</style>



