<template>
    <div v-if="disable" class="JzButton" :style="{background:disabledBg}">
        <label>{{this.title}}</label>
    </div>
    <div v-else class="JzButton" :style="{background:buttonBg}" @click="clickButton">
        <label>{{this.title}}</label>
    </div>
</template>
<script>
import utils from "@/lib/utils"
export default ({
    name: 'JzButton',
    data: () => {
        return {
            buttonBg: utils.blueBgColor,
            disabledBg: utils.disableColor
        }
    },
    props: {
        title: {
            default: "按钮"
        },
        disable: {
            default: false
        }
    },
    methods: {
        clickButton: function() {
            this.$emit("clickButtonHandler")
        }
    },
    mounted:function(){
        
    }
})
</script>
<style scoped>
.JzButton {
    margin: auto 15px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    border-radius: 5px;
    font-size: 17px;
}
</style>


